.skillsBlock {
    background-color: #b2f5c6;
    padding-bottom: 30px;
}

.skillsContainer {
    flex-direction: column;
    justify-content: flex-start;
}

.title {
    background-color: white;
    margin: 10px;
}

.skills {
    border: 1px solid;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    width: 100%;
}
